<template>
  <div class="input-range">
    <label>от</label
    ><BaseNumeric
      class="custom-text-field"
      currency=""
      separator="space"
      v-bind:precision="0"
      v-model="currentValue[0]"
    ></BaseNumeric
    ><label>до</label
    ><BaseNumeric
      class="custom-text-field"
      currency=""
      separator="space"
      v-bind:precision="0"
      v-model="currentValue[1]"
    ></BaseNumeric>
  </div>
</template>

<script>
import BaseNumeric from "./BaseNumeric";

export default {
  name: "BaseInputRange",
  components: {
    BaseNumeric
  },
  props: ["value"],
  data() {
    return {
      currentValue: this.value
    };
  },
  watch: {
    value(newValue) {
      this.currentValue = newValue;
    },
    currentValue(newValue) {
      this.$emit("input", newValue);
    }
  }
};
</script>
