<template>
  <div class="additional-filters">
    <a
      href="#"
      ref="button"
      v-on:click.prevent="togglePopup();"
      class="settings__setting settings__setting-filters flex-column"
    >
      <span
        v-if="hasSetFilters && $store.state.finished"
        class="settings__label"
        >{{ $store.state.cars_count }}
        {{ automobilesString($store.state.cars_count) }} из 245</span
      >
      <span
        v-if="!(hasSetFilters && $store.state.finished)"
        class="settings__label"
        >Расширенные фильтры</span
      >
      <span class="settings__choice">{{ filterString }}</span>
    </a>
    <div class="additional-filters-outer" ref="popup">
      <div class="additional-filters-inner">
        <h3>Основные параметры</h3>
        <div class="row row-main" v-for="row in filter.main">
          <div class="column" v-for="element in row">
            <h4>{{ element.label }}</h4>
            <BaseCheckboxGroup
              v-if="element.type === 'Checkbox'"
              v-bind:options="element.options"
              v-model="element.value"
            ></BaseCheckboxGroup>
            <BaseRadioGroup
              v-else-if="element.type === 'Radio'"
              v-bind:options="element.options"
              v-model="element.value"
            ></BaseRadioGroup>
            <BaseInputRange
              v-else-if="element.type === 'InputRange'"
              v-model="element.value"
            ></BaseInputRange>
            <BaseSlider
              v-else-if="element.type === 'Slider'"
              v-model="element.value"
              v-bind:min="element.min"
              v-bind:max="element.max"
            ></BaseSlider>
            <BaseSelectRange
              v-else-if="element.type === 'SelectRange'"
              v-model="element.value"
              v-bind:min="element.min"
              v-bind:max="element.max"
              v-bind:step="element.step"
            ></BaseSelectRange>
          </div>
        </div>
        <div class="additional" ref="additional">
          <hr />
          <h3>Дополнительные параметры</h3>
          <template v-for="row in filter.advanced">
            <template v-for="element in row">
              <h4>{{ element.label }}</h4>
              <div class="row">
                <div class="column" v-for="optionGroup in element.options">
                  <BaseCheckboxGroup
                    v-if="element.type === 'Checkbox'"
                    v-bind:options="optionGroup"
                    v-model="element.value"
                  ></BaseCheckboxGroup>
                </div>
              </div>
            </template>
            <hr />
          </template>
        </div>
        <div class="row last">
          <a
            v-on:click.prevent="showAdditional();"
            class="all-params"
            href="#"
            ref="showParams"
            >Все параметры<svg
              width="10"
              height="5"
              viewBox="0 0 10 5"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M0 0L5 5L10 0H0Z"
              />
            </svg>
          </a>
          <a
            v-on:click.prevent="hideAdditional();"
            class="hide-params"
            href="#"
            ref="hideParams"
            >Свернуть<svg
              width="10"
              height="5"
              viewBox="0 0 10 5"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M0 5L5 0L10 5H0Z"
              />
            </svg>
          </a>
          <div class="reset-wrapper">
            <a
              v-if="hasSetFilters"
              href="#"
              class="reset"
              v-on:click.stop.prevent="reset();"
              ><svg
                width="20"
                height="20"
                viewBox="0 0 20 20"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  clip-rule="evenodd"
                  d="M10 0C4.47 0 0 4.47 0 10C0 15.53 4.47 20 10 20C15.53 20 20 15.53 20 10C20 4.47 15.53 0 10 0ZM15 13.59L13.59 15L10 11.41L6.41 15L5 13.59L8.59 10L5 6.41L6.41 5L10 8.59L13.59 5L15 6.41L11.41 10L15 13.59Z"
                /></svg
              >Сбросить</a
            >
          </div>
          <Loader v-on:hide-popup="hide();"></Loader>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseRadioGroup from "./base/BaseRadioGroup";
import BaseCheckboxGroup from "./base/BaseCheckboxGroup";
import BaseSlider from "./base/BaseSlider";
import BaseSelectRange from "./base/BaseSelectRange";
import BaseInputRange from "./base/BaseInputRange";
import Loader from "./Loader";

import _ from "lodash";

export default {
  name: "AdditionalFilters",
  data() {
    return {
      filter: {
        main: [],
        advanced: []
      },
      hasSetFilters: false
    };
  },
  components: {
    BaseRadioGroup,
    BaseCheckboxGroup,
    BaseInputRange,
    BaseSelectRange,
    BaseSlider,
    Loader
  },
  computed: {
    filterString() {
      let result = "";

      this.filter.main.forEach(row => {
        row.forEach(element => {
          if (Array.isArray(element.options)) {
            if (Array.isArray(element.value)) {
              element.value.forEach(value => {
                let found = element.options.find(
                  o => parseInt(o.value) === parseInt(value)
                );
                if (found) result += found.label + ", ";
              });
            } else {
              let found = element.options.find(
                o => parseInt(o.value) === parseInt(element.value)
              );
              if (found) result += found.label + ", ";
            }
          }
        });
      });

      this.filter.advanced.forEach(row => {
        row.forEach(element => {
          if (Array.isArray(element.options)) {
            if (Array.isArray(element.value)) {
              element.value.forEach(value => {
                element.options.forEach(optionGroup => {
                  let found = optionGroup.find(
                    o => parseInt(o.value) === parseInt(value)
                  );
                  if (found) {
                    result += found.label + ", ";
                  }
                });
              });
            }
          }
        });
      });

      if (result.length > 2) result = result.substring(0, result.length - 2);

      if (result.length > 23) {
        result = result.substring(0, 21);
        result = result + "...";
      }
      return result;
    }
  },

  methods: {
    reset() {
      for (var k in this.filter) {
        this.filter[k].forEach(row => {
          row.forEach(element => {
            if (Array.isArray(element.defaultValue))
              element.value = element.defaultValue.slice();
            else element.value = element.defaultValue;
          });
        });
      }
    },
    togglePopup() {
      if (this.$refs.popup.style.maxHeight !== "1000px") this.show();
      else this.hide();
    },
    hide() {
      this.$refs.popup.style.maxHeight = "0";
      this.$refs.button.classList.remove("settings__setting-selected");
    },
    show() {
      this.$refs.popup.style.maxHeight = "1000px";
      this.$refs.button.classList.add("settings__setting-selected");
    },
    showAdditional() {
      this.$refs.additional.style.height = "353px";
      this.$refs.showParams.style.display = "none";
      this.$refs.hideParams.style.display = "inline";
    },
    hideAdditional() {
      this.$refs.additional.style.height = "0";
      this.$refs.showParams.style.display = "inline";
      this.$refs.hideParams.style.display = "none";
    }
  },
  mounted() {
    document.addEventListener("click", e => {
      if (
        this.$refs.popup.contains(e.target) ||
        this.$refs.button.contains(e.target)
      )
        return;

      if (this.$refs.popup.style.maxHeight === "1000px") this.hide();
    });
  },
  watch: {
    general(newValue) {
      //populate filters as soon as general info is received
      newValue.filter.main.forEach(row => {
        this.filter.main.push(row);
      });

      this.$store.state.general.filter.advanced.forEach(row => {
        this.filter.advanced.push(row);
      });
    },
    filter: {
      handler: function(v) {
        if (!this.$store.state.initialized) return;
        let filters = {};

        this.hasSetFilters = false;

        this.filter.main.concat(this.filter.advanced).forEach(row => {
          row.forEach(element => {
            if (Array.isArray(element.value)) {
              //console.log(element.value + " default: " + element.defaultValue);
              if (
                element.type === "InputRange" ||
                element.type === "SelectRange"
              ) {
                filters[element.name + "_min"] = element.value[0];
                filters[element.name + "_max"] = element.value[1];
              } else filters[element.name] = element.value;

              if (
                !_.isEqual(
                  _.sortBy(element.value),
                  _.sortBy(element.defaultValue)
                )
              )
                this.hasSetFilters = true;
            } else {
              filters[element.name] = element.value;

              if (parseInt(element.value) !== parseInt(element.defaultValue))
                this.hasSetFilters = true;
            }
          });
        });

        //console.log(filters);

        this.$store.commit("setFilters", filters);
        this.$store.commit("getCarsCount");
      },
      deep: true
    }
  }
};
</script>

<style scoped lang="scss">
@import "./additionalfilters.scss";
</style>
